 

.product-page {
    display: flex;
    flex-wrap: wrap;  
    gap: 20px; 
    padding: 20px;
  }
  
  .product-card {
    width: 200px; 
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    position: relative;
    transition: transform 0.3s;
  }
 
  
  .image-container {
    position: relative;
  }
  
  .image-container img {
    width: 100%;
    height: auto;
  }
  
  .sale-tag {
    position: absolute;
    top: 10px;
    left: 10px;
    background-color: red;
    color: #fff;
    padding: 5px 10px;
    font-size: 0.75rem;
    font-weight: bold;
    border-radius: 4px;
    animation: flash 1s infinite alternate;
  }
  
  @keyframes flash {
    from { opacity: 1; }
    to { opacity: 0.3; }
  }
  
  .product-details {
    padding: 15px;
  }
  
  .product-title {
    font-size: 1.1rem;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .product-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .colors {
    display: flex;
    gap: 5px;
  }
  
  .color-dot {
    width: 5px;
    height: 5px;
    border-radius: 50%;
    border: 1px solid #ddd;
  }
  
  .price-section {
    text-align: right;
  }
  
  .price {
    font-size: 1rem;
    text-decoration: line-through;
    color: #888;
  }
  
  .discount-price {
    font-size: 1.1rem;
    color: #e63946;
    font-weight: bold;
  }
  