 
.footer-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  background-color: #333;
  color: #fff;
}

.footer-left {
  display: flex;
  align-items: center;
}
 
.footer-logo {
  width: 80px;
  height: auto;
  border-radius: 8px;  
  margin-right: 15px;  
}

.footer-left address {
  font-style: normal;
  line-height: 1.5;
}

.footer-right {
  display: flex;
  gap: 15px;
}

.social-icon {
  color: #fff;
  font-size: 24px;
  transition: color 0.3s;
}

.social-icon:hover {
  color: #1da1f2; 
}
.footer-head{
   
  text-align: center;
}
 
